.ModalBackground{
    background-color: rgba(32,35,41,.8);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.ModalBackground h1{
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #ffff;
}